import SearchTool from '~/components/SearchTool'
import QPagination from '~/components/QPagination'
import FloatButtonGroup from '~/components/FloatButtonGroup'
const limit = 20
export default {
  components: { SearchTool, QPagination, FloatButtonGroup },
  asyncData({ params, $axios }) {
    return $axios.post(`/search`, {
      content: params.keyword,
      searchType: 0,
      limit,
      offset: (parseInt(params.page) - 1) * limit
    }).then((data) => {
      return {
        songs: data.songs,
        keyword: params.keyword,
        total: Math.ceil(data.count / limit),
        page: parseInt(params.page)
      }
    }).catch((err) => {
      console.log(err)
      return {
        keyword: params.keyword
      }
    })
  },
  data() {
    return {
      songs: [],
      total: 0,
      page: 1,
      limit
    }
  },
  computed: {
    fmt() {
      return `/search/${this.$route.params.keyword}/:p`
    }
  },
  created() {},
  mounted() {
    // console.log(this.$data)
  },
  methods: {
    openSong(id) {
      window.open(`/song/${id}`, '_blank')
    }
  }
}
